<template>
  <div class="render-wrapper">
    <h2 @click="clickHandler">{{text}}</h2>
    <Funcomp>
      <h2>这是上级传来的消息</h2>
      <p>暗号，怎么写？</p>
    </Funcomp>
  </div>
</template>

<script>
import Funcomp from '@/components/vuecomp/render/fun-comp'

export default {
  components: {
    Funcomp
  },
  data() {
    return {
      text: 'render函数和jsx'
    }
  },
  methods: {
    async clickHandler() {
      // console.log(this)
      this.text = '萧南执'
      console.log(this.$el.textContent === '萧南执这是上级传来的消息暗号，怎么写？')
      // nextTick返回一个Promise
      await this.$nextTick()
      console.log(this.$el.textContent === '萧南执这是上级传来的消息暗号，怎么写？')
    }
  }
}
</script>

<style lang="less" scoped>
.render-wrapper {
  p {
    color: red;
  }
}
</style>